body{padding:10px;margin:0px;font-family:'tahoma'}
.div_zone_color_bg {width: 25px;height: 22px;color: #fff;text-align: center;border-radius: 2px;display:block;line-height:22px;box-shadow: 1px 1px 0px #3c3c3c;}
.title_color_bg {font-size:18px;width: 25px;height: 22px;color: #fff;text-align: center;border-radius: 2px;line-height:22px;padding:2px 10px;box-shadow: 1px 1px 0px #3c3c3c;}
.A1 {background: #990000;}
.A2 {background: #990000;}
.B {background: #522e1a;}
.C {background: #5a5b60;}
.D {background: #db3d7b;}
.E {background: #ffde2c;}
.F {background: #f36e31;}
.G {background: #000000;}
.H {background: #1b2c55;}
.I {background: #00ff00;}
.J {background: #145139;}
.K {background: #5aaebd;}
.PS {background: #000000}
.div_zone_color_bg.GMP {font-size:12px;background: #000000;}
.div_zone_color_bg.OEM {font-size:12px;background: #000000;}
.title_color_bg.GMP {background: #000000;}
.title_color_bg.OEM {background: #444;}
.endtext{display: block;max-width: 98%;white-space: nowrap;overflow: hidden !important;text-overflow: ellipsis;}

#section-header{margin-bottom:10px;display:none;}
.subtitle{background:#66668B;font-weight:bold;color:#fff;padding-bottom:10px;padding-left:15px;display:none;}

#section-body{display: flex;justify-content:row;}
#section-body > div{padding:0px;}
#section-body div#left{flex:4;order:2;}
#section-body div#left .title{display:flex;flex-direction:row;font-size:24px;text-shadow:2px 1px 0px #000;text-align:center;background:#66668B;font-weight:bold;color:#fff;padding:10px 20px;border-top-left-radius:5px;border-top-right-radius:5px;}
#section-body div#left .title .two-item{display:flex;justify-content:center;width:100%;}
#section-body div#left table{width:100%;border-spacing:0px;border:1px solid #444;border-bottom:none;background:#f6f6ff;}
#section-body div#left table tr.first-priority{background:#ffcfcf !important;}
#section-body div#left table th{padding:5px 2px;text-align:center;background:#444444;color:#fff;font-weight:normal;}
#section-body div#left table td{padding:2px 8px;text-align:center;border-bottom:1px solid #444444;}
#section-body div#left table td.left{text-align:left !important;}
#section-body div#left table td.right{text-align:right !important;}
#section-body div#left table td:last-child{border-right:none;}

#section-body div#middle{flex:1;padding:0 10px;order:3;}
#section-body div#middle .title{font-size:24px;text-shadow:2px 1px 0px #000;text-align:center;background:#9CA863;font-weight:bold;color:#fff;	padding:10px 20px;border-top-left-radius:5px;border-top-right-radius:5px;}
#section-body div#middle table{width:100%;border-spacing:0px;border:1px solid #444;border-bottom:none;background:#f2f7dc;}
#section-body div#middle table tr.first-priority{background:#ffcfcf !important;}
#section-body div#middle table th{padding:5px 2px;text-align:center;background:#444444;color:#fff;font-weight:normal;}
#section-body div#middle table td{padding:2px;text-align:center;border-bottom:1px solid #444444}
#section-body div#middle table td.left{text-align:left !important;}
#section-body div#middle table td.right{text-align:right !important;}

#section-body div#right{flex:1;padding:0 10px; order:1;}
#section-body div#right .title{font-size:24px;text-shadow:2px 1px 0px #000;text-align:center;background:#CFCE7A;font-weight:bold;color:#fff;padding:10px 20px;border-top-left-radius:5px;border-top-right-radius:5px;}
#section-body div#right table{width:100%;border-spacing:0px;border:1px solid #444;border-bottom:none;background:#fffed2;}
#section-body div#right table tr.first-priority{background:#ffcfcf !important;}
#section-body div#right table th{padding:5px 2px;text-align:center;background:#444444;color:#fff;font-weight:normal;}
#section-body div#right table td{padding:2px;text-align:center;border-bottom:1px solid #444444}
#section-body div#right table td.left{text-align:left !important;}
#section-body div#right table td.right{text-align:right !important;}

